import './AdminPedidos.css'

import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page, SDataTable } from '../../components'
import { useStores } from '../../models/root-store/root-store-context'

const moment = require('moment-timezone')
const numeral = require('numeral')

export const AdminBalance: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const DataTable = React.createRef<any>()
  const DataTableFacturado = React.createRef()
  const DataTableCobrado = React.createRef()
  const [Balance, setBalance] = useState({ facturado: 0, pendiente_facturacion: 0, cobrado: 0 })

  const visualizar = (row: any) => {
    history.push('/admin/orden_venta/detail/' + row.id)
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}>
          <FIcon.Eye size={14} />
        </span>
      </div>
    )
  }

  const setup = async () => {
    obtenerBalance()
  }

  React.useEffect(() => {
    setup()
  }, [])

  const obtenerBalance = () => {
    const tipo = loginStore.modo_uso_app === 30 ? 20 : 10
    rootStore.environment.api.getBalanceDetail(tipo).then((result) => {
      if (result.kind === 'ok') {
        setBalance(result.data)
      }
    })
  }

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h2 className="text-themecolor">Balance</h2>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Balance</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0" style={{ backgroundColor: '#303AB2' }}>
            <div className="card-body">
              <div className={'row'}>
                <div className={'col-4'}>
                  <h3 className={'text-white font-weight-bold'}>Pendiente de facturación</h3>
                  <h3 className={'text-white'}>Gs. {numeral(Balance.pendiente_facturacion).format('0,0')}</h3>
                </div>
                <div className={'col-4'}>
                  <h3 className={'text-white font-weight-bold'}>Facturado</h3>
                  <h3 className={'text-white'}>Gs. {numeral(Balance.facturado).format('0,0')}</h3>
                </div>
                <div className={'col-4'}>
                  <h3 className={'text-white font-weight-bold'}>Cobrado</h3>
                  <h3 className={'text-white'}>Gs. {numeral(Balance.cobrado).format('0,0')}</h3>
                </div>
              </div>
            </div>
          </div>
          <h2 className="text-themecolor my-4">Pendiente de facturación</h2>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  const tipo = loginStore.modo_uso_app === 30 ? 20 : 10
                  return rootStore.environment.api.balanceList(page, perPage, order, search, false, false, tipo)
                }}
                columns={[
                  {
                    name: 'Fecha',
                    selector: 'fecha',
                    sortable: true,
                    cell: (row: any) => moment(row.fecha).format('DD/MM/YYYY'),
                  },
                  {
                    name: 'Descripción',
                    selector: 'referencia',
                    sortable: false,
                    cell: (row: any) => 'Movimiento orden ' + row.descripcion,
                  },
                  {
                    name: 'Monto',
                    selector: 'monto',
                    sortable: false,
                    cell: (row: any) => {
                      return 'Gs. ' + numeral(row.monto).format('0,0')
                    },
                  },

                  {
                    name: 'Proveedor',
                    sortable: false,
                    cell: (row: any) => row.fabricante,
                    omit: !loginStore.administrador,
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    omit: loginStore.modo_uso_app === 30,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
          <h2 className="text-themecolor my-4">Facturado</h2>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTableFacturado}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  const tipo = loginStore.modo_uso_app === 30 ? 20 : 10
                  return rootStore.environment.api.balanceList(page, perPage, order, search, false, true, tipo)
                }}
                columns={[
                  {
                    name: 'Fecha',
                    selector: 'fecha',
                    sortable: true,
                    cell: (row: any) => moment(row.fecha).format('DD/MM/YYYY'),
                  },
                  {
                    name: 'Descripción',
                    selector: 'referencia',
                    sortable: false,
                    cell: (row: any) => 'Movimiento orden ' + row.descripcion,
                  },
                  {
                    name: 'Monto',
                    selector: 'monto',
                    sortable: false,
                    cell: (row: any) => {
                      return 'Gs. ' + numeral(row.monto).format('0,0')
                    },
                  },

                  {
                    name: 'Proveedor',
                    sortable: false,
                    cell: (row: any) => row.fabricante,
                    omit: !loginStore.administrador,
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    omit: loginStore.modo_uso_app === 30,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
          <h2 className="text-themecolor my-4">Cobrado</h2>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTableCobrado}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  const tipo = loginStore.modo_uso_app === 30 ? 20 : 10
                  return rootStore.environment.api.balanceList(page, perPage, order, search, true, true, tipo)
                }}
                columns={[
                  {
                    name: 'Fecha',
                    selector: 'fecha',
                    sortable: true,
                    cell: (row: any) => moment(row.fecha).format('DD/MM/YYYY'),
                  },
                  {
                    name: 'Descripción',
                    selector: 'referencia',
                    sortable: false,
                    cell: (row: any) => 'Movimiento orden ' + row.descripcion,
                  },
                  {
                    name: 'Monto',
                    selector: 'monto',
                    sortable: false,
                    cell: (row: any) => {
                      return 'Gs. ' + numeral(row.monto).format('0,0')
                    },
                  },

                  {
                    name: 'Fabricante',
                    sortable: false,
                    cell: (row: any) => row.fabricante,
                    omit: !loginStore.administrador,
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    omit: loginStore.modo_uso_app === 30,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminBalance
